h2 {
  color: #FD9881;
}

.images-section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  gap: 47px;
  padding-bottom: 40px;
}

.images-section img {
  width: 350px;
  height: 350px;
  border-radius: 15px;
  transition: transform 0.1s;
}

.images-section img:hover {
  transform: scale(1.05);
}

.images-section img {
  opacity: 1;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.images-section img:hover {
  opacity: 0.5;
}

#section-fleche3 {
  position: absolute;
  height: 330px;
  width: 330px;
  top: 1919px;
  left: -57px;
}

.image-with-title {
  position: relative;
}

.image-with-title::before {
  content: attr(title);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px;
  opacity: 0;
  transition: opacity 0.3s;
}

.image-with-title:hover::before {
  opacity: 1;
}/*# sourceMappingURL=portfolio.css.map */