$darkPink : #FD9881;
$green : #3CC3B5;

.mobile-menu-button {
    display: none;
    cursor: pointer;
}

.bar {
    height: 3px;
    width: 45px;
    background-color: #FD9881;
    margin: 9px 0;
    transition: 0.4s;
}

.bar1.bar1 {
    transform: rotate(-45deg) translate(-5px, 6px);
}

.bar2.bar2 {
    opacity: 0;
}

.bar3.bar3 {
    transform: rotate(45deg) translate(-5px, -6px);
}

.header-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* padding-top: 25px;
    padding-bottom: 40px; */
    /* padding-left: 65px; */
    padding-right: 5%;
    background-color: white;
    position: fixed;
    width: 100%;
    filter: drop-shadow(0 0 0.10rem rgb(156, 151, 151));
    z-index: 1000;

    img {
        width: 100%;
        border-radius: 55%;
        margin-top: 5px;
        margin-left: 44%;
    }

}

#image-link {
    width: 5%;
    display: flex;
    align-content: center;
    justify-content: flex-start;
    margin-right: 45%;
    padding-bottom: 4px;
}

.logo-container {
    width: fit-content;
}

.links-container {
    display: flex;
    gap: 15%;
    padding-top: 5px;

    a {
        color: $darkPink;
        text-decoration: none;
        gap: 5px;
        font-family: 'Montserrat', sans-serif;
        font-size: 20px;
        white-space: nowrap;

    }

    a :visited {
        color: $darkPink;
    }

    a:hover {
        color: #FEBCAE;
    }
}

.reso2 {
    width: 10%;
    border-radius: 0px;
    padding-right: 15px;

    img {
        width: 21%;
        margin-left: 13px;
        border-radius: 0px;
    }

    a {
        width: 12%;
    }
}

.links-container.mobile-menu-open {
    position: fixed;
    height: 100vh;
    background-color: #FFEEEB;
    overflow-y: auto;
}