// Responsive

@media screen and (max-width : 1358px) {

    .reso2 {
        margin-left: 11%;
    }
}


@media screen and (max-width : 1500px) {

    .mobile-menu-button {
        display: block;
        margin-right: 2%;
        border: none;
        background-color: white;
    }

    .links-container {
        display: none;
        flex-direction: column;
        background-color: white;
        width: 100%;
        position: absolute;
        top: 76px;
        left: 0;
        z-index: 1;

        a {
            font-size: 60px;
            // padding-bottom: 40px;
            // padding-top: 69px;
        }
    }

    .links-container.mobile-menu-open {
        display: flex;
    }

    .reso2 {
        display: none;
    }

    // #image-link {
    //     width: 9%;
    // }

    .Banner-home img {
        padding-top: 240px;
    }

    .Banner-home h1 {
        padding-top: 286px;
    }

}



@media screen and (max-width : 500px) {
    #image-link {
        width: 11%;
    }


    .links-container {
        top: 61px;

        a {
            font-size: 50px;
            // padding-bottom: 30px;
        }
    }


    h2 {
        font-size: 35px;
    }

}

@media (min-width : 500px) and (max-width : 800px) {
    .reso {
        width: 18%;
    }

    .reso img {
        width: 24%;
        padding-top: 23px;
        margin-left: 10px;
    }
}


@media screen and (max-width : 800px) {
    .simple-footer {
        flex-direction: column-reverse;
        align-items: center;
    }

    .reso {
        width: 36%;
    }

    .simple-footer p {
        padding-top: 0px;
    }


    #image-link2 img {
        display: none;
    }
}

@media screen and (max-width : 1023px) {
    .links-container.mobile-menu-open {
        top: 61px;
    }
}

@media screen and (min-width : 1024px) {
    .links-container.mobile-menu-open {
        top: 60px
    }
}