/* @media screen and (max-width: 600px) and (max-width:1024px) {


} */


@media screen and (max-width : 1348px) {
    .Banner-home h1 {
        font-size: 3rem;
    }
}

@media screen and (max-width : 853px) {
    .Banner-home img {
        display: none;
    }

    .Banner-home h1 {
        padding-right: 0px;
        font-size: 2.8rem;
    }

}

@media screen and (max-width : 382px) {

.Banner-home h1 {
font-size: 2.5rem;
}

}