.Banner-ex h2 {
    color: #FD9881;
    font-size: 40px;
    padding-top: 45px;
}

.Banner-ex ul {
    color: #FD9881;
    font-size: 32px;
    text-align: center;
    padding-left: 200px;
    padding-right: 200px;
    padding-bottom: 30px;
    padding-top: 4px;
    line-height: 1.3;
    list-style-type: none;

    a {
        text-decoration: none;
        position: relative;
        color: #fd9881;
    }

    a:visited {
        color: #FD9881;
    }

    a:hover::before {
        bottom: 0;
        height: 100%;

    }

    a::before {
        content: '';
        background-color: #b1e7e2;
        position: absolute;
        left: 0;
        bottom: 3px;
        width: 100%;
        height: 8px;
        z-index: -1;
        transition: all .3s ease-in-out;
        color: white;
    }
}

