@media screen and (max-width : 1500px) {

    .Banner-ex ul {
        padding-left: 50px;
        padding-right: 50px;
    }
}

@media screen and (max-width : 500px) {

    .Banner-ex {
        ul {
            padding-left: 10px;
            padding-right: 10px;
            font-size: 22px;
        }

        h2 {
            font-size: 35px;
        }
    }
}